import React, { useRef, useState } from 'react';
//import { View, Text, ScrollView, TouchableOpacity } from 'react-native';
//import jsonData from '../books/TheWorstJourneyInTheWorld_clean.json';
import { View, Text, TouchableOpacity, StyleSheet, Dimensions, ScrollView, Image} from 'react-native';
import books from '../books/book_loader.js'
import Select from 'react-select';


const MainScreen = () => {
  const passageScrollViewRef = useRef(null);
  const outputScrollViewRef = useRef(null);
  const [passageCoords, setPassageCoords] = useState([]);
  const [outputCoords, setOutputCoords] = useState([]);
  const [selectedItemIndex, setSelectedItemIndex] = useState(0);
  const [selected_book, setSelectedBook] = useState(
    'The Worst Journey in the World by Apsley Cherry-Garrard'
    );

  const options = Object.keys(books).map(key => ({
    value: key,
    label: key
  }));
  
  const handleBook = (selectedOption) => {
    setSelectedBook(selectedOption.value)
  }

  const handlePassageItemClick = (index) => {
    if (outputScrollViewRef.current) {
      outputScrollViewRef.current.scrollTo({ y: outputCoords[index], animated: true });
    }
    if (selectedItemIndex == index) {
        setSelectedItemIndex(null)
    } else {
        setSelectedItemIndex(index)
    }
  };

  const handleOutputItemClick = (index) => {
    if (passageScrollViewRef.current) {
      passageScrollViewRef.current.scrollTo({ y: passageCoords[index], animated: true });
    }
    if (selectedItemIndex == index) {
        setSelectedItemIndex(null)
    } else {
        setSelectedItemIndex(index)
    }
  };
  return (
    <View style={styles.container}>

      <View style={styles.bar}>
        <Text style={styles.bartext}>Book:</Text>
        <Select
        options={options}
        defaultValue={options[0]}
        onChange={handleBook}
        />
      </View>

      <View style={styles.content}>
      <ScrollView
        ref={passageScrollViewRef}
        style={{ flex: 1 }}
        contentContainerStyle={{ padding: 10 }}
      >
        {books[selected_book].map((item, index) => (
          <TouchableOpacity
            key={index}
            style={[
                styles.basic_item,
                selectedItemIndex === index ? styles.selected_item : null
            ]}
            onPress={() => handlePassageItemClick(index)}
            onLayout={(event) => {
                const layout = event.nativeEvent.layout;
                passageCoords[index] = layout.y;
                setPassageCoords(passageCoords);
            }}
          >
            <View style={{ flexDirection: 'row', alignItems: 'top'}}>
                <Text style={{ marginLeft: 10 , marginRight: 10, fontWeight: 'bold'}}>{item.merge}</Text>
                <Text selectable={true}>{item.passage}</Text>
            </View>
          </TouchableOpacity>
        ))}
      </ScrollView>

      <ScrollView
        ref={outputScrollViewRef}
        style={{ flex: 1 }}
        contentContainerStyle={{ padding: 10 }}
      >
        {books[selected_book].map((item, index) => (
            <TouchableOpacity
            style={[
                styles.basic_item,
                selectedItemIndex === index ? styles.selected_item : null
            ]}
            key={index}
            onPress={() => handleOutputItemClick(index)}
            onLayout={(event) => {
                const layout = event.nativeEvent.layout;
                outputCoords[index] = layout.y;
                setOutputCoords(outputCoords);
            }}>
                <View style={{ flexDirection: 'row', alignItems: 'top' }}>
                    <Text style={{ marginLeft: 10 , marginRight: 10, fontWeight: 'bold'}}>{item.merge}</Text>
                    <Text selectable={true}>{item.parsed.summary}</Text>
                </View>

                {selectedItemIndex == index && (
                    <View style={{ padding: 10 }}>
                    <Text style={{ fontWeight: 'bold' }}>Excerpt 1:</Text>
                    <Text style={{ padding: 10 }} selectable={true}>{item.parsed.excerpt_1}</Text>
                    <Text style={{ fontWeight: 'bold' }}>Excerpt 2:</Text>
                    <Text style={{ padding: 10 }} selectable={true}>{item.parsed.excerpt_2}</Text>
                    <Text style={{ fontWeight: 'bold' }}>Text-to-Image Prompt:</Text>
                    <Text style={{ padding: 10 }} selectable={true}>{item.parsed.text_to_image}</Text>
                    {
                        !(item.image === null) && (
                            <Image
                            style={{ height: 500 }}
                            source={require(`../images/${item.image}`)} 
                            resizeMode="contain"
                            />
                        )
                    }
                    </View>
                    
                )}

            </TouchableOpacity>
        ))}
      </ScrollView>
      </View>
    </View>
  );
};

const height = Dimensions.get('window').height;

const styles = StyleSheet.create({
    container: {
      //height: height,
      //flexDirection: 'row',
    },
    content: {
        height: height - 50,
        flexDirection: 'row',
        marginTop: 50
    },
    basic_item: {
        margin: 2,
    },
    selected_item: {
        borderWidth: 1,
        borderColor: '#ccc',
    },
    bar: {
        flexDirection: 'row',
        backgroundColor: '#f2f2f2',
        height: 50,
        justifyContent: 'left',
        alignItems: 'center',
        position: 'absolute',
        top: 0,
        left: 0,
        right: 0,
        zIndex: 9999, // Set a high zIndex value to ensure it appears above other content
      },
      bartext: {
        padding: 10,
        fontSize: 18, // Customize the font size of the text
        fontWeight: 'bold', // Customize the font weight of the text
      },
});

export default MainScreen;
